@keyframes opaque {
	0% {
		opacity: 0
	}

	to {
		opacity: 1
	}
}

@keyframes resizeanim {

	0%,
	to {
		opacity: 0
	}
}

.apexcharts-canvas {
	position: relative;
	direction: ltr !important;
	user-select: none
}

.apexcharts-canvas ::-webkit-scrollbar {
	-webkit-appearance: none;
	width: 6px
}

.apexcharts-canvas ::-webkit-scrollbar-thumb {
	border-radius: 4px;
	background-color: rgba(0, 0, 0, .5);
	box-shadow: 0 0 1px rgba(255, 255, 255, .5);
}

.apexcharts-inner {
	position: relative
}

.apexcharts-text tspan {
	font-family: inherit
}

rect.legend-mouseover-inactive,
.legend-mouseover-inactive rect,
.legend-mouseover-inactive path,
.legend-mouseover-inactive circle,
.legend-mouseover-inactive line,
.legend-mouseover-inactive text.apexcharts-yaxis-title-text,
.legend-mouseover-inactive text.apexcharts-yaxis-label {
	transition: .15s ease all;
	opacity: .2
}

.apexcharts-legend-text {
	padding-left: 15px;
	margin-left: -15px;
}

.apexcharts-series-collapsed {
	opacity: 0
}

.apexcharts-tooltip {
	border-radius: 5px;
	box-shadow: 2px 2px 6px -4px #999;
	cursor: default;
	font-size: 14px;
	inset-block-start: 62px;
	opacity: 0;
	pointer-events: none;
	position: absolute;
	top: 20px;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	white-space: nowrap;
	z-index: 12;
	transition: .15s ease all;
}


.apexcharts-tooltip.apexcharts-active {
	opacity: 1;
	transition: .15s ease all
}

[data-bs-theme="light"] .apexcharts-tooltip {
	border: 1px solid #e3e3e3;
	background: rgba(255, 255, 255, .96)
}

[data-bs-theme="dark"] .apexcharts-tooltip {
	color: #fff;
	background: rgba(30, 30, 30, .9);
	box-shadow: 2px 2px 6px -4px #000;
	/* border: 1px solid #000; */
}


.apexcharts-tooltip * {
	font-family: inherit
}

.apexcharts-tooltip-title {
	padding: 6px;
	font-size: 15px;
	margin-bottom: 4px
}

[data-bs-theme="light"] .apexcharts-tooltip .apexcharts-tooltip-title {
	background: #eceff1;
	border-bottom: 1px solid #ddd
}

[data-bs-theme="dark"] .apexcharts-tooltip .apexcharts-tooltip-title {
	background: rgba(0, 0, 0, .7) !important;
	border-bottom: 1px solid #000 !important;
}

.apexcharts-tooltip-text-goals-value,
.apexcharts-tooltip-text-y-value,
.apexcharts-tooltip-text-z-value {
	display: inline-block;
	margin-left: 5px;
	font-weight: 600
}

.apexcharts-tooltip-text-goals-label:empty,
.apexcharts-tooltip-text-goals-value:empty,
.apexcharts-tooltip-text-y-label:empty,
.apexcharts-tooltip-text-y-value:empty,
.apexcharts-tooltip-text-z-value:empty,
.apexcharts-tooltip-title:empty {
	display: none
}

.apexcharts-tooltip-text-goals-label,
.apexcharts-tooltip-text-goals-value {
	padding: 6px 0 5px
}

.apexcharts-tooltip-goals-group,
.apexcharts-tooltip-text-goals-label,
.apexcharts-tooltip-text-goals-value {
	display: flex
}

.apexcharts-tooltip-text-goals-label:not(:empty),
.apexcharts-tooltip-text-goals-value:not(:empty) {
	margin-top: -6px
}

.apexcharts-tooltip-marker {
	width: 12px;
	height: 12px;
	position: relative;
	background-color: currentColor;
	margin-right: 10px;
	border-radius: 50%
}

.apexcharts-tooltip-series-group {
	padding: 0 10px;
	display: none;
	text-align: left;
	justify-content: left;
	align-items: center
}

.apexcharts-tooltip-series-group.apexcharts-active .apexcharts-tooltip-marker {
	opacity: 1
}

.apexcharts-tooltip-series-group.apexcharts-active,
.apexcharts-tooltip-series-group:last-child {
	padding-bottom: 4px
}

.apexcharts-tooltip-y-group {
	padding: 6px 0 5px
}

.apexcharts-custom-tooltip,
.apexcharts-tooltip-box {
	padding: 4px 8px
}

.apexcharts-tooltip-boxPlot {
	display: flex;
	flex-direction: column-reverse
}

.apexcharts-tooltip-box>div {
	margin: 4px 0
}

.apexcharts-tooltip-box span.value {
	font-weight: 700
}

.apexcharts-tooltip-rangebar {
	padding: 5px 8px
}

.apexcharts-tooltip-rangebar .category {
	font-weight: 600;
	color: #777
}

.apexcharts-tooltip-rangebar .series-name {
	font-weight: 700;
	display: block;
	margin-bottom: 5px
}

.apexcharts-xaxistooltip,
.apexcharts-yaxistooltip {
	opacity: 0;
	pointer-events: none;
	color: #373d3f;
	font-size: 13px;
	text-align: center;
	border-radius: 2px;
	position: absolute;
	z-index: 10;
	background: #eceff1;
	border: 1px solid #90a4ae
}

.apexcharts-xaxistooltip {
	padding: 9px 10px;
	transition: .15s ease all
}

[data-bs-theme="dark"] .apexcharts-xaxistooltip {
	background: rgba(0, 0, 0, .7) !important;
	border: 1px solid rgba(0, 0, 0, .5) !important;
	color: #fff !important;
}

.apexcharts-xaxistooltip:after,
.apexcharts-xaxistooltip:before {
	left: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none
}

.apexcharts-xaxistooltip:after {
	border-color: transparent;
	border-width: 6px;
	margin-left: -6px
}

.apexcharts-xaxistooltip:before {
	border-color: transparent;
	border-width: 7px;
	margin-left: -7px
}

.apexcharts-xaxistooltip-bottom:after,
.apexcharts-xaxistooltip-bottom:before {
	bottom: 100%
}

.apexcharts-xaxistooltip-top:after,
.apexcharts-xaxistooltip-top:before {
	top: 100%
}

.apexcharts-xaxistooltip-bottom:after {
	border-bottom-color: #eceff1
}

.apexcharts-xaxistooltip-bottom:before {
	border-bottom-color: #90a4ae
}

[data-bs-theme="dark"] .apexcharts-xaxistooltip-bottom:after,
[data-bs-theme="dark"] .apexcharts-xaxistooltip-bottom:before {
	border-bottom-color: rgba(0, 0, 0, .5)
}

.apexcharts-xaxistooltip-top:after {
	border-top-color: #eceff1
}

.apexcharts-xaxistooltip-top:before {
	border-top-color: #90a4ae
}

[data-bs-theme="dark"] .apexcharts-xaxistooltip-top:after,
[data-bs-theme="dark"] .apexcharts-xaxistooltip-top:before {
	border-top-color: rgba(0, 0, 0, .5)
}

.apexcharts-xaxistooltip.apexcharts-active {
	opacity: 1;
	transition: .15s ease all
}

.apexcharts-yaxistooltip {
	padding: 4px 10px
}

[data-bs-theme="dark"] .apexcharts-yaxistooltip {
	background: rgba(0, 0, 0, .7) !important;
	border: 1px solid rgba(0, 0, 0, .5) !important;
	color: #fff !important;
}

.apexcharts-yaxistooltip:after,
.apexcharts-yaxistooltip:before {
	top: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none
}

.apexcharts-yaxistooltip:after {
	border-color: transparent;
	border-width: 6px;
	margin-top: -6px
}

.apexcharts-yaxistooltip:before {
	border-color: transparent;
	border-width: 7px;
	margin-top: -7px
}

.apexcharts-yaxistooltip-left:after,
.apexcharts-yaxistooltip-left:before {
	left: 100%
}

.apexcharts-yaxistooltip-right:after,
.apexcharts-yaxistooltip-right:before {
	right: 100%
}

.apexcharts-yaxistooltip-left:after {
	border-left-color: #eceff1
}

.apexcharts-yaxistooltip-left:before {
	border-left-color: #90a4ae
}

[data-bs-theme="dark"] .apexcharts-yaxistooltip-left:after,
[data-bs-theme="dark"] .apexcharts-yaxistooltip-left:before {
	border-left-color: rgba(0, 0, 0, .5)
}

.apexcharts-yaxistooltip-right:after {
	border-right-color: #eceff1
}

.apexcharts-yaxistooltip-right:before {
	border-right-color: #90a4ae
}

[data-bs-theme="dark"] .apexcharts-yaxistooltip-right:after,
[data-bs-theme="dark"] .apexcharts-yaxistooltip-right:before {
	border-right-color: rgba(0, 0, 0, .5)
}

.apexcharts-yaxistooltip.apexcharts-active {
	opacity: 1
}

.apexcharts-yaxistooltip-hidden {
	display: none
}

.apexcharts-xcrosshairs,
.apexcharts-ycrosshairs {
	pointer-events: none;
	opacity: 0;
	transition: .15s ease all
}

.apexcharts-xcrosshairs.apexcharts-active,
.apexcharts-ycrosshairs.apexcharts-active {
	opacity: 1;
	transition: .15s ease all
}

.apexcharts-ycrosshairs-hidden {
	opacity: 0
}

.apexcharts-selection-rect {
	cursor: move
}

.svg_select_shape {
	stroke-width: 1;
	stroke-dasharray: 10 10;
	stroke: black;
	stroke-opacity: 0.1;
	pointer-events: none;
	fill: none;
}

.svg_select_handle {
	stroke-width: 3;
	stroke: black;
	fill: none;
}

.svg_select_handle_r {
	cursor: e-resize;
}

.svg_select_handle_l {
	cursor: w-resize;
}

.apexcharts-svg.apexcharts-zoomable.hovering-zoom {
	cursor: crosshair
}

.apexcharts-svg.apexcharts-zoomable.hovering-pan {
	cursor: move
}

/* .apexcharts-menu-icon,
  .apexcharts-pan-icon,
  .apexcharts-reset-icon,
  .apexcharts-selection-icon,
  .apexcharts-toolbar-custom-icon,
  .apexcharts-zoom-icon,
  .apexcharts-zoomin-icon,
  .apexcharts-zoomout-icon {
	cursor: pointer;
	width: 20px;
	height: 20px;
	line-height: 24px;
	color: #6e8192;
	text-align: center
  } */

.apexcharts-menu-icon svg,
.apexcharts-reset-icon svg,
.apexcharts-zoom-icon svg,
.apexcharts-zoomin-icon svg,
.apexcharts-zoomout-icon svg {
	fill: #6e8192
}

.apexcharts-selection-icon svg {
	fill: #444;
}

[data-bs-theme="dark"] .apexcharts-menu-icon svg,
[data-bs-theme="dark"] .apexcharts-pan-icon svg,
[data-bs-theme="dark"] .apexcharts-reset-icon svg,
[data-bs-theme="dark"] .apexcharts-selection-icon svg,
[data-bs-theme="dark"] .apexcharts-toolbar-custom-icon svg,
[data-bs-theme="dark"] .apexcharts-zoom-icon svg,
[data-bs-theme="dark"] .apexcharts-zoomin-icon svg,
[data-bs-theme="dark"] .apexcharts-zoomout-icon svg {
	fill: #f3f4f5
}

.apexcharts-canvas .apexcharts-reset-zoom-icon.apexcharts-selected svg,
.apexcharts-canvas .apexcharts-selection-icon.apexcharts-selected svg,
.apexcharts-canvas .apexcharts-zoom-icon.apexcharts-selected svg {
	fill: #008ffb
}

[data-bs-theme="light"] .apexcharts-menu-icon:hover svg,
[data-bs-theme="light"] .apexcharts-reset-icon:hover svg,
[data-bs-theme="light"] .apexcharts-selection-icon:not(.apexcharts-selected):hover svg,
[data-bs-theme="light"] .apexcharts-zoom-icon:not(.apexcharts-selected):hover svg,
[data-bs-theme="light"] .apexcharts-zoomin-icon:hover svg,
[data-bs-theme="light"] .apexcharts-zoomout-icon:hover svg {
	fill: #333
}

.apexcharts-menu-icon,
.apexcharts-selection-icon {
	position: relative
}

.apexcharts-pan-icon.apexcharts-selected svg {
	stroke: var(--primary-500);
}

.apexcharts-pan-icon:not(.apexcharts-selected):hover svg {
	stroke: var(--primary-500);
}

.apexcharts-toolbar {
	position: absolute;
	z-index: 11;
	max-width: 176px;
	text-align: right;
	border-radius: 3px;
	padding: 0 6px 2px;
	display: flex;
	justify-content: end;
	align-items: center;
	gap: 0.25rem;
	right: 0 !important;
}

.apexcharts-toolbar .sa-icon {
	--sa-icon-color: #333;
	--sa-icon-size: 0.85rem;
}

[data-bs-theme="dark"] .apexcharts-toolbar .sa-icon {
	--sa-icon-color: #fff;
	stroke-width: 2px;
}

.apexcharts-menu {

	--bs-dropdown-min-width: 124px;
	--bs-dropdown-font-size: 0.85rem;
	--bs-dropdown-padding-x: 0;
	--bs-dropdown-padding-y: 0.5rem;
	--bs-dropdown-border-width: var(--bs-border-width);
	--bs-dropdown-border-color: var(--bs-border-color-translucent);
	--bs-dropdown-border-radius: var(--bs-border-radius);
	--bs-dropdown-border-width: var(--bs-border-width);

	background: var(--bs-body-bg);
	color: var(--bs-body-color);
	font-size: var(--bs-dropdown-font-size);
	position: absolute;
	top: 100%;
	border: var(--bs-dropdown-border-width) solid var(--bs-dropdown-border-color);
	border-radius: var(--bs-dropdown-border-radius);
	padding: var(--bs-dropdown-padding-y) var(--bs-dropdown-padding-x);
	right: 10px;
	opacity: 0;
	min-width: var(--bs-dropdown-min-width);
	transition: .15s ease all;
	pointer-events: none;

}

.apexcharts-menu.apexcharts-menu-open {
	opacity: 1;
	pointer-events: all;
	transition: .15s ease all
}

.apexcharts-menu-item {
	padding: 0.5rem 0.75rem;
	font-size: 0.825rem;
	cursor: pointer
}

[data-bs-theme="light"] .apexcharts-menu-item:hover {
	background: var(--app-nav-item-hover-bg);
	color: var(--primary-500);
}

[data-bs-theme="dark"] .apexcharts-menu {
	background: rgba(0, 0, 0, .7) !important;
	color: #fff !important;
}

@media screen and (min-width:768px) {
	.apexcharts-canvas:hover .apexcharts-toolbar {
		opacity: 1;
		display: flex;
	}
}

.apexcharts-canvas .apexcharts-element-hidden,
.apexcharts-datalabel.apexcharts-element-hidden,
.apexcharts-hide .apexcharts-series-points {
	opacity: 0;
	display: none;
}

.apexcharts-hidden-element-shown {
	opacity: 1;
	transition: 0.25s ease all;
}

.apexcharts-datalabel,
.apexcharts-datalabel-label,
.apexcharts-datalabel-value,
.apexcharts-datalabels,
.apexcharts-pie-label {
	cursor: default;
	pointer-events: none
}

.apexcharts-pie-label-delay {
	opacity: 0;
	animation-name: opaque;
	animation-duration: .3s;
	animation-fill-mode: forwards;
	animation-timing-function: ease
}

.apexcharts-radialbar-label {
	cursor: pointer;
}

.apexcharts-annotation-rect,
.apexcharts-area-series .apexcharts-area,
.apexcharts-gridline,
.apexcharts-line,
.apexcharts-point-annotation-label,
.apexcharts-radar-series path:not(.apexcharts-marker),
.apexcharts-radar-series polygon,
.apexcharts-toolbar svg,
.apexcharts-tooltip .apexcharts-marker,
.apexcharts-xaxis-annotation-label,
.apexcharts-yaxis-annotation-label,
.apexcharts-zoom-rect,
.no-pointer-events {
	pointer-events: none
}

.apexcharts-tooltip-active .apexcharts-marker {
	transition: .15s ease all
}

.resize-triggers {
	animation: 1ms resizeanim;
	visibility: hidden;
	opacity: 0;
	height: 100%;
	width: 100%;
	overflow: hidden
}

.contract-trigger:before,
.resize-triggers,
.resize-triggers>div {
	content: " ";
	display: block;
	position: absolute;
	top: 0;
	left: 0
}

.resize-triggers>div {
	height: 100%;
	width: 100%;
	background: #eee;
	overflow: auto
}

.contract-trigger:before {
	overflow: hidden;
	width: 200%;
	height: 200%
}

.apexcharts-bar-goals-markers {
	pointer-events: none
}

.apexcharts-bar-shadows {
	pointer-events: none
}

.apexcharts-rangebar-goals-markers {
	pointer-events: none
}